import { createGoogleFormSubmitter } from "./google-form";

/**
 * Match actual Google Form fields (from your form and cURL).
 */
export interface NominationForm {
  id : string;
  website: string;
  awards: string;
  name: string;
  license: string;
  gender: string;
  impact: string;
  organization: string;
  type: string;
  email: string;
  role: string;
  experience: string;
  achievements: string;
  dobYear: string;
  dobMonth: string;
  dobDay: string;
  nationality: string;
  missionVision: string;
  secondaryEmail: string;
}

/**
 * Map each field to its Google Form entry.xxx ID.
 */
export const nominationForm = createGoogleFormSubmitter<NominationForm>({
  formUrl:
    "https://docs.google.com/forms/u/0/d/e/1FAIpQLSeOHDrzWj0eG4CNxBzIsF-imnViRjSSZLMOwUIw0KaPIfbjkw/formResponse",

  entryMap: {
    id : 'entry.646679894',
    website: "entry.265225496",
    awards: "entry.486072342",
    name: "entry.734809119",
    license: "entry.995725479",
    gender: "entry.1049319968",
    impact: "entry.1195029161",
    organization: "entry.1277288765",
    type: "entry.1324479162",
    email: "entry.1330670166",
    role: "entry.1357697266",
    experience: "entry.1544591627",
    achievements: "entry.1633791441",
    dobYear: "entry.1902106913_year",
    dobMonth: "entry.1902106913_month",
    dobDay: "entry.1902106913_day",
    nationality: "entry.1943240344",
    missionVision: "entry.2142260550",
    secondaryEmail: "entry.1865334252",
  },
});

/**
 * Test runner with labeled dummy data so you can visually confirm
 * each field in your Google Form response sheet.
 */
export async function testNominationForm() {
  console.log("🧪 Starting Google Form submission test...");

  const dummyData: NominationForm = {
    id: Math.floor(1000000000 + Math.random() * 9000000000).toString(), // 🔥 random 10-digit ID
    website: "https://www.example.com",
    awards: "Awards – YES",
    name: "Nathan Neuhaus",
    license: "License – test",
    gender: "Gender – male",
    impact: "Impact – massive",
    organization: "Organization – Tinh Hoa Viet Mafia",
    type: "Type – individual",
    email: "Email – test@gmail.com",
    role: "Role – CTO",
    experience: "Experience – YES",
    achievements: "Achievements – YES",
    dobYear: "1990",
    dobMonth: "10",
    dobDay: "27",
    nationality: "Nationality – My Tho",
    missionVision: "Mission/Vision – to see the light",
    secondaryEmail: "Canididate Email – test2@gmail.com",
  };


  try {
    const res = await nominationForm.submit(dummyData);
    console.log("✅ Form submitted successfully:", res.status);
  } catch (err) {
    console.error("❌ Submission failed:", err);
  }
}
