export function createGoogleFormSubmitter<T extends Record<string, any>>(config: {
  formUrl: string;
  entryMap: Partial<Record<keyof T, string>>; // 👈 change here
}) {
  return {
    async submit(data: Partial<T>): Promise<Response> {
      const params = new URLSearchParams();
      for (const [key, value] of Object.entries(data)) {
        const entryId = config.entryMap[key as keyof T];
        if (entryId && value != null) params.append(entryId, value.toString());
      }
      params.append("fvv", "1");
      params.append("pageHistory", "0");
      params.append("fbzx", `${Date.now()}`);
      const res = await fetch(config.formUrl, {
        method: "POST",
        headers: { "Content-Type": "application/x-www-form-urlencoded" },
        body: params.toString(),
      });
      if (!res.ok) throw new Error(`Form submission failed: ${res.status} ${res.statusText}`);
      return res;
    },
  };
}
